.calculator-card {
    padding: 20px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.calculator-row {
    width: 100%;
    display: flex;
    align-items: center;
    flex: 1;
    margin-bottom: 16px;
}

.calculator-row-label {
    width: 30%;
    margin-right: 12px;
    font-size: 18px;
}

.calculator-form {
    display: flex;
    flex-direction: column;
}

.calculator-btn {
    margin-top: 20px;
}

@media only screen and (max-width: 1360px) {
    .calculator-card {
        width: 84vw;
        padding: 10px 3vw;
    }
}
